export class ColorBar {
    constructor(element, options) {
        this._padding = 10;
        this._showSlider = true;
        this._moveEvt = false;
        this.setCoordinateByEvent = (e) => {
            this._showSlider = true;
            this.setCoordinate(e.layerY);
        };
        this.handleMouseMove = (e) => {
            if (e.which === 1) {
                this.setCoordinateByEvent(e);
            }
        };
        const _options = {
            width: null,
            height: null,
            lineWidth: 1,
            onColorChange: null
        };
        if (options) {
            Object.assign(_options, options);
        }
        this._options = _options;
        this._el = element;
        this.init();
    }
    init() {
        const padding = this._padding;
        const canvas = this._el, ctx = canvas.getContext('2d');
        const { width, height } = this._options;
        const gradient = ctx.createLinearGradient(padding, padding, padding, height - padding * 2);
        const colors = ColorBar.COLORS, length = colors.length;
        canvas.setAttribute('width', width.toString());
        canvas.setAttribute('height', height.toString());
        // 填充背景色
        colors.forEach((color, index) => {
            gradient.addColorStop(index / (length - 1), color);
        });
        this._gradient = gradient;
        this._ctx = ctx;
        this._width = width;
        this._height = height;
        ctx.lineWidth = this._options.lineWidth;
        canvas.addEventListener('mousedown', (e) => {
            if (e.which === 1) {
                this._moveEvt = true;
                this.setCoordinateByEvent(e);
                canvas.addEventListener('mousemove', this.handleMouseMove);
            }
        });
        document.addEventListener('mouseup', (e) => {
            if (this._moveEvt && e.which === 1) {
                this._moveEvt = false;
                canvas.removeEventListener('mousemove', this.handleMouseMove);
            }
        });
        this.setCoordinate(0);
    }
    // 这里背景色不会改变,可以复用CanvasGradient
    fill() {
        const gradient = this._gradient, ctx = this._ctx, padding = this._padding;
        ctx.fillStyle = gradient;
        ctx.fillRect(this._padding, this._padding, this._width - padding * 2, this._height - padding * 2);
    }
    setCoordinate(y) {
        const padding = this._padding;
        let currentY;
        if (y < padding) {
            currentY = padding;
        }
        else if (y > this._height - padding) {
            currentY = this._height - padding - 1;
        }
        else {
            currentY = y;
        }
        this._y = currentY;
        this.draw();
    }
    // 渲染光标
    renderCurrentColor() {
        // 可以选择是否显示光标
        if (this._showSlider) {
            const lineWidth = this._options.lineWidth, x = this._width / 2, y = this._y, sliderWidth = this._width, sliderHeight = lineWidth * 6;
            if (y !== undefined) {
                const ctx = this._ctx;
                ctx.save();
                ctx.strokeStyle = '#000';
                ctx.strokeRect(lineWidth / 2, y - sliderHeight / 2, sliderWidth - lineWidth, sliderHeight);
                ctx.strokeStyle = '#fff';
                ctx.strokeRect(lineWidth + lineWidth / 2, y - sliderHeight / 2 + lineWidth, sliderWidth - lineWidth * 3, sliderHeight - lineWidth * 2);
                ctx.stroke();
                ctx.restore();
                if (this._options.onColorChange) {
                    const data = ctx.getImageData(x, y, 1, 1);
                    this._options.onColorChange(data);
                }
            }
        }
    }
    draw() {
        const ctx = this._ctx;
        ctx.clearRect(0, 0, this._width, this._height);
        this.fill();
        this.renderCurrentColor();
    }
    // 隐藏光标
    hideSlider() {
        if (this._showSlider) {
            this._showSlider = false;
            this.draw();
        }
    }
}
// 拾色条的颜色渐变顺序
ColorBar.COLORS = [
    'rgb(255, 0, 0)',
    'rgb(255, 255, 0)',
    'rgb(0, 255, 0)',
    'rgb(0, 255, 255)',
    'rgb(0, 0, 255)',
    'rgb(255, 0, 255)',
    'rgb(255, 0, 0)'
];



