export default class Tween {
    constructor(options) {
        this._spend = 0;
        this._complete = false;
        const { x, y } = options;
        this._init = { x, y };
        this._current = { x, y };
        this._options = options;
    }
    to(position, time) {
        this._next = position;
        this._time = time;
        return this;
    }
    start() {
        this._prevTime = Date.now();
        this.update();
        return this;
    }
    update() {
        const now = Date.now();
        this._spend = this._spend + (now - this._prevTime);
        if (this._spend >= this._time) {
            this._complete = true;
            return;
        }
        this._prevTime = now;
        const k = this._spend / this._time;
        this._current = {
            x: this._init.x + (this._next.x - this._init.x) * k,
            y: this._init.y + (this._next.y - this._init.y) * k
        };
        this._options.onUpdate(this._current);
    }
    get complete() {
        return this._complete;
    }
}



